<template>
  <div>
    <Nav :push-down="true"/>
    <div>
      <div class="container row">
        <div class="col-lg-3">
          <AdminNav/>
        </div>
        <div class="col-lg-9">
          <div class="content-card">
            <h2></h2>
            <form @submit="search($event)">
                <input class="search" type="search" v-model="keyword" placeholder="" autofocus>
                <button type="submit" hidden></button>
            </form>
            <button @click="show_form = !show_form" class="btn btn-outline-dark tool-bar"></button>
            <form v-if="show_form" @submit="cou($event)">
                    <div class="input-control">
                        <label></label>
                        <input v-validator="rule" type="text" v-model="current.username">
                    </div>
                    <div class="input-control">
                        <label></label>
                        <input type="text" v-model="current.realname">
                    </div>
                    <div class="input-control">
                        <label></label>
                        <input v-validator="'required|min_length:6|max_length:64'" type="password" v-model="current.password">
                    </div>
                   <div class="input-control row">
                       <button class="btn btn-outline-secondary" type="submit"></button>
                       <button class="btn btn-outline-secondary" @click="show_form = false" type="button"></button>
                   </div>
                </form>
                <div class="table">
                    <table v-if="!show_form">
                    <thead>
                        <th></th>
                        <th></th>
                        <th></th>
                    </thead>
                    <tbody>
                        <tr :key="index" v-for="(row,index) in list">
                            <td>{{row.username}}</td>
                            <td>{{row.realname}}</td>
                            <td>
                                <button class="btn-small operate" @click="remove(row.id)"></button>
                                <button class="btn-small operate" @click="set_current(row)"></button>
                            </td>
                        </tr>
                    </tbody>
              </table>
            </div>
          </div>
          <Pagination :showForm="!show_form" :totalCount="total" :limit="limit" :onChange="on_page_change"/>
        </div>
      </div>
    </div>
    <Footer/>
  </div>
</template>

<script>
  import AdminPage from '../../mixins/AdminPage';

  export default {
       data() {
         return {
           model: 'user',
           searchable : ['username','realname'],
         }
       },
       computed : {
      rule () {
        let def = {
          required   : true,
          username   : true,
          min_length : 4,
          max_length : 18,
          not_exist  : [ 'user', 'username' ],
        };

        if (this.is_update()) {
          def.not_exist.push(this.current.username);
        }
        return def;
      },
    },
       mixins: [AdminPage],
  }
</script>

<style scoped>

  .table {
    overflow: auto;
  }

  th, td {
    padding: 5px 10px;
    text-align: left;
  }

</style>
